.Problem {
    width: 90%;
    max-width: 600px;
    margin: 0px auto;
}

.prblemName {
    text-align: center;
    font-size: 40px;
    font-weight: 600;
    margin-top: 50px;
    margin-bottom: 20px;
}

.titleDes {
    margin-bottom: 30px;
    width: 100%;
}

.desName {
    margin-left: 7px;
    font-size: 22px;
    font-weight: 400;
}

.problemDes {
    width: 100%;
    height: auto;
    padding: 6px 12px;
    border: 1px solid gray;
    border-radius: 10px;
    margin-top: 10px;
    box-sizing: border-box;
}

.doubleDes {
    display: flex;
    gap: 15px;
}

.titleDes.left {
    margin-right: 7px;
    text-align: end;
}

select.desName {
    font-size: 17px;
    padding: 6px 12px;
    border-radius: 10px;
}

.codeForm {
    width: 100%;
    min-height: 300px;
    padding: 6px 12px;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 10px;
    box-sizing: border-box;
    margin-bottom: 20px;
    resize: none;
    font-size: 15px;
    background-color: rgb(65, 61, 61);
    color: rgb(250, 169, 107);
}

.submitCode {
    background-color: rgb(74, 165, 221);
    margin-bottom: 200px;
    width: 80px;
    text-align: center;
    padding: 6px 12px;
    border-radius: 10px;
    cursor: pointer;
}